import { API_URIS } from "./configs";
import { Store } from "./store";

/**
 * Obtiene los items por iteracin a partir de una lista de IDs.
 *
 * @param {string[]} idsList - Lista de IDs de los items a obtener.
 * @returns {Promise<Object[]>} - Lista de resultados de los items, o un array vaco si no hay IDs.
 */
export async function getItemsByIteration(idsList) {
  if (!idsList || idsList.length === 0) return null;
  const results = [];

  for (const item of idsList) {
    try {
      const response = await fetch(`${API_URIS.BASE_URL}${API_URIS.ITEM}${item}`);

      if (response.ok) {
        const data = await response.json();
        results.push(data);
      } else {
        if (response.status === 401) {
          console.error("Error de autorizacin: Access Token no vlido.");
          // Detener la ejecucin en caso de error de autorizacin
          return { error: "Unauthorized", details: "Access Token no vlido." };
        }
        console.error(`Error fetching item ${item}: ${response.statusText}`);
        results.push({ error: "Request Error", details: response.statusText });
      }
    } catch (error) {
      console.error(`Failed to fetch item ${item}:`, error);
      results.push({ error: "Network Error", details: error.message });
    }
  }

  return results;
}

/**
 * Obtiene los items por sus IDs usando el token de acceso.
 *
 * @param {string[]} itemIds - Lista de IDs de los items a obtener.
 * @param {string} accessToken - Token de acceso para la autorizacin.
 * @returns {Promise<Object[]|{error: string, details: string}|null>} - Datos de los items, o un objeto con error en caso de fallo.
 */
export async function fetchItemsByIds(itemIds, accessToken) {
  try {
    const baseUrl = "https://api.mercadolibre.com/items";
    const idsParam = itemIds.join(",");
    const url = `${baseUrl}?ids=${idsParam}`;

    const response = await fetch(url, {
      method: "GET",
      headers: {
        Authorization: `Bearer ${accessToken}`,
      },
    });

    if (!response.ok) {
      if (response.status === 401) {
        console.error("Error de autorizacin: Access Token no vlido.");
        // Detener la ejecucin en caso de error de autorizacin
        return { error: "Unauthorized", details: "Access Token no vlido." };
      }
      throw new Error(`Error en la solicitud: ${response.statusText}`);
    }

    const data = await response.json();
    return data;
  } catch (error) {
    console.error("Error al obtener los items:", error);
    return { error: "Request Error", details: error.message };
  }
}

/**
 * Busca items por regin y palabra clave en varias pginas.
 *
 * @param {string} search - Palabra clave para la bsqueda.
 * @param {string} region - Regin para la bsqueda.
 * @param {number} pages - Nmero de pginas a buscar.
 * @param {string} [accessToken] - Token de acceso para la autorizacin (opcional).
 * @returns {Promise<Object[]|{error: string, details: string}|null>} - Lista de resultados de bsqueda, o un objeto con error en caso de fallo.
 */
export async function searchItemsByRegion(search, region, pages, accessToken) {
  try {
    let result = [];

    for (let page = 1; page <= pages; page++) {
      try {
        const headers = {
          Accept: "application/json",
          "Content-Type": "application/json",
        };

        if (accessToken) {
          headers["Authorization"] = `Bearer ${accessToken}`;
        }

        const response = await fetch(`${API_URIS.BASE_URL}${API_URIS.SEARCH(region, search, page)}`, {
          method: "GET",
          headers: headers,
        });

        if (response.ok) {
          const data = await response.json();
          result = result.concat(data?.results);
        } else {
          if (response.status === 401) {
            console.error("Error de autorizacin: Access Token no vlido.");
            return { error: "Unauthorized", details: "Access Token no vlido." };
          }
          console.error(`Error fetching page ${page}: ${response.statusText}`);
        }
      } catch (error) {
        console.error(`Error fetching page ${page}: ${error.message}`);
        // En caso de error en una pgina, continuar con las siguientes pginas
      }
    }

    return result;
  } catch (error) {
    console.error("Error al buscar items:", error);
    return { error: "Request Error", details: error.message };
  }
}

/**
 * Abre una ventana de autenticacin en una URL especfica.
 *
 * @param {string} regionURL - URL de la regin para la autenticacin.
 * @param {string} clientId - ID del cliente para la autenticacin.
 * @param {string} redirectUrl - URL de redireccin despus de la autenticacin.
 */
export function openAuthWindow(regionURL, clientId, redirectUrl) {
  const baseUrl = `${regionURL}/authorization?response_type=code&client_id=${clientId}&redirect_uri=${redirectUrl}`;
  console.log("Base URL de la autenticacin:", baseUrl);
  window.open(baseUrl, "_blank");
}

/**
 * Obtiene un token de acceso usando el cdigo de autorizacin.
 *
 * @param {string} appId - ID de la aplicacin.
 * @param {string} secretKey - Clave secreta de la aplicacin.
 * @param {string} authorizationCode - Cdigo de autorizacin recibido.
 * @param {string} redirectUri - URL de redireccin despus de la autenticacin.
 * @param {string} codeVerifier - Verificador de cdigo usado en el intercambio.
 * @returns {Promise<Object|{error: string, details: string}|null>} - Objeto con el access_token y refresh_token, o un objeto con error en caso de fallo.
 */
export async function getAccessToken(appId, secretKey, authorizationCode, redirectUri, codeVerifier) {
  try {
    const url = "https://api.mercadolibre.com/oauth/token";

    const response = await fetch(url, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/x-www-form-urlencoded",
      },
      body: new URLSearchParams({
        grant_type: "authorization_code",
        client_id: appId,
        client_secret: secretKey,
        code: authorizationCode,
        redirect_uri: redirectUri,
        code_verifier: codeVerifier,
      }),
    });

    if (!response.ok) {
      throw new Error("Error en la solicitud de acceso: " + response.statusText);
    }

    const data = await response.json();

    if (data.access_token) {
      return { accessToken: data.access_token, refreshToken: data.refresh_token };
    } else {
      throw new Error("No se pudo obtener el access_token");
    }
  } catch (error) {
    console.error("Error en la solicitud de acceso:", error);
    return { error: "Access Token Request Error", details: error.message };
  }
}

/**
 * Renueva el access_token y refresh_token usando el refresh_token existente.
 *
 * @param {string} appId - ID de la aplicacin.
 * @param {string} secretKey - Clave secreta de la aplicacin.
 * @param {string} refreshToken - El refresh_token existente.
 * @returns {Promise<Object|{error: string, details: string}|null>} - Objeto con el nuevo access_token y refresh_token, o un objeto con error en caso de fallo.
 */
export async function refreshAccessToken(appId, secretKey, refreshToken) {
  try {
    const url = "https://api.mercadolibre.com/oauth/token";

    const response = await fetch(url, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/x-www-form-urlencoded",
      },
      body: new URLSearchParams({
        grant_type: "refresh_token",
        client_id: appId,
        client_secret: secretKey,
        refresh_token: refreshToken,
      }),
    });

    if (!response.ok) {
      throw new Error("Error al renovar el token: " + response.statusText);
    }

    const data = await response.json();

    if (data.access_token) {
      setApiKeySettings({
        ...Store.settingsApiKey,
        accessToken: data.access_token,
        refreshToken: data.refresh_token || refreshToken,
      });

      return { accessToken: data.access_token, refreshToken: data.refresh_token };
    } else {
      throw new Error("No se pudo renovar el access_token");
    }
  } catch (error) {
    console.error("Error al renovar el access_token:", error);
    return { error: "Token Refresh Error", details: error.message };
  }
}
